/* 1. Main styles */
.main{
    text-align: center;
    margin: 0 auto;
    display: flex;
    flex-direction: column;
    align-items: center;
}

.container{
    display: inline-block;
    height: 100%;
    text-align: left;
    width: 75ch;
    overflow: hidden;
}

@media screen and (max-width: 640px) {
    .container { width: 50ch; };
    .text-container { width: 45ch;}
}

.text-container{
    float: left;
    line-height: 1.5;
    padding-left: 0.5rem;
    font-family: 'Mulish', sans-serif;
}
