/* 1. Navbar styles */
.navbar{
    position: -webkit-sticky;
    position: sticky;
    font-family: 'Mulish', sans-serif;
    display: inline-block;
    overflow: hidden;
    background-color: #777;
    height: -webkit-fit-content;
    height: -moz-fit-content;
    height: fit-content;
    width: 100%;
}

.navbar 
.nav-item{
    float: right;
}

.nav-item{
    text-decoration: none;
    color: #000;
    padding: 1.5ch;
    margin-right: 0.5%;
}

.nav-item:hover{
    color: rgb(0, 0, 0, 0.25);
}

/* 2. Banner styles */
.banner{
    font-size: 6ch;
    padding-top: 1%;
    padding-bottom: 1%;
}
